.benableMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}
.benableMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 20px;
  margin-right: 0px;
  background-color: #0066FF;
  color: #FFF;
  border: 2px solid #0066FF;
}
.benableMain .header button:hover {
  background-color: #003399;
  border: 2px solid #003399;
  cursor: pointer;
}
.benableMain .header button:disabled {
  background-color: #8599ad;
  color: #FFF;
  border: 2px solid #8599ad;
  cursor: not-allowed;
}

.benableMain .result input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 165px;
}
.benableMain .result button {
  padding: 10px;
  font-size: 16px;
  margin: 20px;
  margin-right: 0px;
  background-color: #0066FF;
  color: #FFF;
  border: 2px solid #0066FF;
}
.benableMain .result button:hover {
  background-color: #003399;
  border: 2px solid #003399;
  cursor: pointer;
}

.benableMain .header img {
  max-height: 10%;
  max-width: 10%;
  float: right;
}

.pyramid .zone {
   height: 7vmin;
   width: 100%;
   background-color: #990000;
   border-top: 5px solid #ffffff;
   text-align: center;
   padding-top: 15px;
   color: #ffffff;
   -webkit-transition: background 1.5s ease;
   -moz-transition: background 1.5s ease;
   -ms-transition: background 1.5s ease;
   -o-transition: background 1.5s ease;
   transition: background 1.5s ease;
}

.pyramid .zone:nth-child(1) {
   clip-path: polygon(30% 0, 70% 0, 75% 100%, 25% 100%);
}
.pyramid .zone:nth-child(2) {
   clip-path: polygon(25% 0, 75% 0, 80% 100%, 20% 100%);
}
.pyramid .zone:nth-child(3) {
   clip-path: polygon(20% 0, 80% 0, 85% 100%, 15% 100%);
}
.pyramid .zone:nth-child(4) {
   clip-path: polygon(15% 0, 85% 0, 90% 100%, 10% 100%);
}
.pyramid .zone:nth-child(5) {
   clip-path: polygon(10% 0, 90% 0, 95% 100%, 5% 100%);
}
.pyramid .zone:nth-child(6) {
   clip-path: polygon(5% 0, 95% 0, 100% 100%, 0 100%);
   border-bottom: 5px solid #ffffff;
}

